import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Col, Row, Spin } from 'antd';
import BlogCardTwo from '../UiComponents/CommonUiComponents/BlogCardTwo';
import { Helmet } from 'react-helmet';
const AllBlogs = () => {
  const [data, setdata] = useState();
  const [spin, setspin] = useState(false);
  useEffect(() => {
    setspin(true);
    axios
      .get('https://ginnovation-server.herokuapp.com/api/blog')
      .then((resp) => {
        setspin(false);
        setdata(resp.data);
      })
      .catch((err) => {
        setspin(false);
        console.log(err);
      });
  }, []);
  return (
    <div className='all-blogs'>
      <Helmet>
        <title>Blogs of Ginnovation</title>
        <meta
          name='description'
          content='Tech Blogs, Creative Writtings | Here is a list of creative blogs written by the ginnovation club members that will open up your thinking'
        />
      </Helmet>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <div className='page-top-secondVarient'>
        <header>
          <h2>Our Blogs</h2>
          <p>Creative writtings from the creative people</p>
        </header>
      </div>
      <div className='container blog-container'>
        {data && (
          <Row gutter={8}>
            {data.map((value, index) => {
              return (
                <Col md={8} key={index}>
                  <BlogCardTwo value={value} />
                </Col>
              );
            })}
          </Row>
        )}
      </div>
    </div>
  );
};

export default AllBlogs;
