import React, { useEffect, useState } from 'react';
import { CalendarOutlined } from '@ant-design/icons';
import { Link } from 'react-router-dom';
import ParseTime from '../../Functions/ParseTime';
import axios from 'axios';
import ReactHtmlParser from 'react-html-parser';

const BlogCardTwo = ({ value }) => {
  const [userdata, setuserdata] = useState();
  useEffect(() => {
    axios
      .get(
        `https://ginnovation-server.herokuapp.com/api/profile/users/${value.creatorsID}`
      )
      .then((resp) => {
        setuserdata(resp.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [value.creatorsID]);

  const cardTitle = value.blogTitle.slice(0, 28) + '...';

  const fallbackImg =
    'https://www.elegantthemes.com/blog/wp-content/uploads/2019/03/png-jpg-gif-featured-image.jpg';
  const coverpic = value.coverpic;

  const cover = {
    backgroundImage: `url(${
      coverpic.substring(0, 4) == 'data' || coverpic.substring(0, 4) == 'http'
        ? value.coverpic !== undefined
          ? value.coverpic
          : fallbackImg
        : fallbackImg
    })`,
  };

  const link = `blog/${value._id}`;

  return (
    <div className='blog-card-two'>
      <div className='card-top' style={cover}>
        <span className='tag'>About</span>
      </div>
      <div className='card-content'>
        {userdata && (
          <div
            className='dp'
            style={{
              backgroundImage: `url(${userdata.profilePicture})`,
            }}
          ></div>
        )}
        <h5>{cardTitle}</h5>
        <p className='sub-heading'>{value.subTitle}</p>
        <div className='c-body'>
          <div className='content'>{ReactHtmlParser(value.blogContent)}</div>
          <Link to={link}>
            <p className='link'>...Read More</p>
          </Link>
        </div>
        {userdata && (
          <em className='author'>
            By {userdata.fullname !== null ? userdata.fullname : 'username'}
          </em>
        )}
        <div className='card-bottom'>
          <span>
            <CalendarOutlined className='icon' />
            {ParseTime(value.createdAt)}
          </span>
          <Link to={link}>
            <button>Full Article</button>
          </Link>
        </div>
      </div>
    </div>
  );
};

export default BlogCardTwo;
